
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    user-select: none;
  }
  .bg-img {
  background: url('https://www.shutterstock.com/image-photo/abstract-blur-image-background-shopping-600nw-1906991296.jpg');

    height: 100vh;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  
  .bg-img:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
  }
  
  .content {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 999;
    text-align: center;
    padding: 60px 32px;
    width: 370px;
    transform: translate(-50%, -50%);
    background: rgba(255, 255, 255, 0.04);
    box-shadow: -1px 4px 28px 0px rgba(0, 0, 0, 0.75);
  }
  
  .content header {
    color: rgb(0, 0, 0);
    font-size: 33px;
    font-weight: 600;
    margin: 0 0 35px 0;
  }
  
  .field {
    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background: rgba(255, 255, 255, 0.94);
    margin-bottom: 15px;
  }
  
  .field span {
    color: #222;
    width: 40px;
    line-height: 45px;
  }
  
  .field input {
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
  }
  
  .space {
    margin-top: 16px;
  }
  
  .show {
    position: absolute;
    right: 13px;
    font-size: 13px;
    font-weight: 700;
    color: #222;
    display: none;
    cursor: pointer;
  }
  
  .pass-key:valid ~ .show {
    display: block;
  }
  
  
  .pass {
    text-align: left;
    margin: 10px 0;
  }
  
  .pass a {
    color: white;
    text-decoration: none;
  }
  
  .pass:hover a {
    text-decoration: underline;
  }
  
  .field input[type="submit"] {
    background: #3498db;
    border: 1px solid #2691d9;
    color: white;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 600;
    cursor: pointer;
  }
  
  .field input[type="submit"]:hover {
    background: #2691d9;
  }
  
  .login {
    color: white;
    margin: 20px 0;
  }
  
  .links {
    display: flex;
    cursor: pointer;
    color: white;
    margin: 0 0 20px 0;
  }
  
  .links i {
    font-size: 17px;
  }
  
  i span {
    margin-left: 8px;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 16px;
  }
  .signup {
    font-size: 15px;
    color: white;
  }
  
  .signup a {
    color: #3498db;
    text-decoration: none;
  }
  
  .signup a:hover {
    text-decoration: underline;
  }
  